.page {
  font-family: Inter, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.4em;
  color: $color-main;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform $animation-time;

    &:target {
      transform: translateX(0);
    }
  }
}
